<template>
  <div>
    <v-breadcrumbs
      :items="items"
      divider="/"
    ></v-breadcrumbs>
    <v-data-table
      :search="search"
      :headers="headers"
      :items="filteredUsers"
      item-key="full_name"
      class="table-rounded"
      :loading="loadingPages"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title>Users List</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            fullscreen
            transition="dialog-bottom-transition"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
                @click="openModal()"
              >
                New
              </v-btn>
            </template>
            <v-card>
              <v-toolbar
                dark
                color="primary"
              >
                <v-btn
                  icon
                  dark
                  @click="closeModal()"
                >
                  <v-icon>{{ icons.mdiClose }}</v-icon>
                </v-btn>
                <v-toolbar-title v-if="!isupdate">
                  Create
                </v-toolbar-title>
                <v-toolbar-title v-if="isupdate">
                  Update
                </v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      sm="12"
                      md="12"
                    >
                      <v-stepper v-model="e1">
                        <v-stepper-header>
                          <v-stepper-step
                            :complete="e1 > 1"
                            step="1"
                          >
                            User Information
                          </v-stepper-step>

                          <v-divider></v-divider>

                          <v-stepper-step
                            :complete="e1 > 2"
                            step="2"
                          >
                            User Priviliges
                          </v-stepper-step>
                        </v-stepper-header>

                        <v-stepper-items>
                          <v-stepper-content step="1">
                            <v-card
                              class="mb-12"
                              color="grey lighten-1"
                            >
                              <v-container>
                                <v-row>
                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.firstname"
                                      label="Enter First name"
                                      required
                                    ></v-text-field>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.middlename"
                                      label="Enter Middle name"
                                      required
                                    ></v-text-field>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.lastname"
                                      label="Enter Last name"
                                      required
                                    ></v-text-field>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.suffix"
                                      label="Enter Suffix"
                                      required
                                    ></v-text-field>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-select
                                      v-model="data.gender"
                                      :items="gender"
                                      label="Select Gender"
                                      required
                                    ></v-select>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.birthdate"
                                      type="date"
                                      required
                                    ></v-text-field>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.telephone_number"
                                      label="Enter Telephone Number"
                                      required
                                    ></v-text-field>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-textarea
                                      v-model="data.address"
                                      label="Enter Address"
                                      required
                                    ></v-textarea>
                                  </v-col>

                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.email"
                                      label="Enter E-mail"
                                      required
                                    ></v-text-field>
                                  </v-col>
                                  <v-col
                                    cols="12"
                                    md="3"
                                  >
                                    <v-text-field
                                      v-model="data.password"
                                      type="password"
                                      label="Enter Password"
                                      required
                                    ></v-text-field>
                                  </v-col>
                                </v-row>
                              </v-container>
                            </v-card>
                            <v-btn
                              color="primary"
                              @click="e1 = 2"
                            >
                              Continue
                            </v-btn>

                            <v-btn
                              text
                              @click="closeModal()"
                            >
                              Cancel
                            </v-btn>
                          </v-stepper-content>

                          <v-stepper-content step="2">
                            <v-card
                              class="mb-12"
                              color="grey lighten-1"
                              height="200px"
                            ></v-card>

                            <v-btn
                              v-if="!isupdate"
                              :loading="loading"
                              :disabled="loading"
                              color="primary"
                              @click="create()"
                            >
                              Save
                            </v-btn>
                            <v-btn
                              v-if="isupdate"
                              :loading="loading"
                              :disabled="loading"
                              color="primary"
                              @click="update()"
                            >
                              Update
                            </v-btn>

                            <v-btn
                              text
                              @click="closeModal()"
                            >
                              Cancel
                            </v-btn>
                          </v-stepper-content>
                        </v-stepper-items>
                      </v-stepper>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-toolbar>
        <v-text-field
          v-model="search"
          label="Search..."
          class="mx-4"
        ></v-text-field>
      </template>
      <template #[`item.fullname`]="{item}">
        {{ item.firstname }} {{ item.middlename }} {{ item.lastname }} {{ item.suffix }}
      </template>
      <template #[`item.usertype`]="{item}">
        {{ item.usertype.type }}
      </template>
      <template #[`item.status`]="{item}">
        <v-chip
          small
          :color="statusColor[item.status.status]"
          class="font-weight-medium"
        >
          {{ item.status.status }}
        </v-chip>
      </template>
      <template #[`item.actions`]="{item}">
        <v-icon
          small
          @click="selectUpdate(item)"
        >
          {{ icons.mdiBookEdit }}
        </v-icon>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import sweetalert2 from '@/plugins/sweetalert2'
import {
  mdiSquareEditOutline, mdiDotsVertical, mdiBookEdit, mdiClose,
} from '@mdi/js'
import {
  mapGetters,
  mapActions,
} from 'vuex'

export default {
  setup() {
    const statusColor = {
      /* eslint-disable key-spacing */
      Pending: 'warning',
      Approved: 'success',
      Declined: 'error',
      /* eslint-enable key-spacing */
    }

    return {
      isupdate: false,
      dialog: false,
      loadingPages: true,
      loading: false,
      items: [
        {
          text: 'Settings',
          disabled: true,
          href: '#',
        },
        {
          text: 'Users',
          disabled: false,
          href: '#',
        },
      ],
      search: '',
      headers: [
        { text: 'CODE', value: 'code' },
        { text: 'FULLNAME', value: 'fullname' },
        { text: 'GENDER', value: 'gender' },
        { text: 'BIRTHDATE', value: 'birthdate' },
        { text: 'TELEPHONE #', value: 'telephone_number' },
        { text: 'ADDRESS', value: 'address' },
        { text: 'EMAIL', value: 'email' },
        { text: 'USER TYPE', value: 'usertype' },
        { text: 'STATUS', value: 'status' },
        { text: 'ACTIONS', value: 'actions', sortable: false },
      ],

      // icons
      icons: {
        mdiSquareEditOutline,
        mdiDotsVertical,
        mdiBookEdit,
        mdiClose,
      },
      statusColor,

      // Stepper Number
      e1: 1,

      // Gender Data
      gender: [
        { text: 'Male' },
        { text: 'Female' },
      ],

      // Input field
      data: {
        id: null,
        firstname: '',
        lastname: '',
        middlename: '',
        suffix: '',
        gender: '',
        birthdate: '',
        telephone_number: '',
        address: '',
        email: '',
        password: '',
        usertype: '',
        status: '',
      },
    }
  },
  computed: {
    filteredUsers() {
      let items = []
      items = this.users.filter(e => e.usertype_id !== 6)

      return items
    },
    ...mapGetters(['users']),
  },
  mounted() {
    this.getUsers().then(() => {
      this.loadingPages = false
    })
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    openModal() {
      this.isupdate = false
      this.data = {
        id: null,
        course: '',
      }
    },
    create() {
      this.loading = true
      this.createUsers(this.data).then(res => {
        this.loading = false
        if (res.data.status === 200) {
          sweetalert2('success', res.data.message)
          this.closeModal()
        } else {
          sweetalert2('warning', res.data.message)
        }
      })
    },
    update() {
      this.loading = true
      this.updateUsers(this.data).then(res => {
        this.loading = false
        if (res.data.status === 200) {
          sweetalert2('success', res.data.message)
          this.closeModal()
        } else {
          sweetalert2('warning', res.data.message)
        }
      })
    },
    selectUpdate(item) {
      this.data = {
        id: item.id,
        firstname: item.firstname,
        lastname: item.lastname,
        middlename: item.middlename,
        suffix: item.suffix,
        gender: item.gender,
        birthdate: item.birthdate,
        telephone_number: item.telephone_number,
        address: item.address,
        email: item.email,
        password: '',
        usertype: item.usertype_id,
        status: item.status_id,
      }
      this.dialog = true
      this.isupdate = true
      this.e1 = 1
    },
    closeModal() {
      this.dialog = false
      this.isupdate = false
      this.e1 = 1
      this.data = {
        id: null,
        firstname: '',
        lastname: '',
        middlename: '',
        suffix: '',
        gender: '',
        birthdate: '',
        telephone_number: '',
        address: '',
        email: '',
        password: '',
        usertype: '',
        status: '',
      }
    },
    ...mapActions(['getUsers', 'createUsers', 'updateUsers']),
  },
}
</script>
